import React from "react";
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import "react-tabs/style/react-tabs.css";

const Portfolio = () => {
  // eslint-disable-next-line no-unused-vars
  const data = [
    {
      url: "https://github.com/ahmdselim/Editorial",
      name: "Editorial",
      category: "component",
      image:
        "http://designazure.com/wp-content/uploads/2017/04/editorial-free-blog-html5-template-2.jpg",
    },
    {
      url: "https://ahmdselim.github.io/QuizApp",
      name: "Quiz App",
      category: "component",
      image:
        "https://s3.envato.com/files/263409728/Preview_Image/Quiz_menu.png",
    },
    {
      url: "https://github.com/ahmdselim/inventory-app",
      name: "Inventory App",
      category: "component",
      image:
        "https://www.zenventory.com/wp-content/uploads/2017/03/CustomOrderSearch-1024x598.png",
    },
    {
      url: "https://ecommercecart-fd1de.web.app",
      name: "Ecommerce Cart",
      category: "component",
      image:
        "https://cdn.dribbble.com/users/9691/screenshots/2838928/shoppingcart2x.png",
    },
    {
      url: "https://github.com/ahmdselim/yanfaa-site",
      name: "Yanfaa Site",
      category: "site",
      image:
        "https://cdn.dribbble.com/users/458182/screenshots/11791464/media/058c67371abc06243829e3f696512a83.jpg?compress=1&resize=400x300",
    },
    {
      url: "https://royalcup-f301f.web.app/",
      name: "Royal Cup Coffee",
      category: "site",
      image:
        "https://colorlib.com/wp/wp-content/uploads/sites/2/12_coffee-shop-websites.jpg",
    },
  ];
  return (
    <section className="portfolio">
      <h1>Portfolio</h1>
      <h2>My Work</h2>
      <Tabs>
        <TabList>
          <Tab>All</Tab>
          <Tab>Components</Tab>
          <Tab>sites</Tab>
        </TabList>
        <TabPanel>
          <div className="sites">
            {Object.keys(data).map((sites, i) => (
              <div key={i}>
                <a href={data[sites].url}>
                  <img src={data[sites].image} />
                </a>
                <a href={data[sites].url}>
                  <p>{data[sites].name}</p>
                </a>
              </div>
            ))}
          </div>
        </TabPanel>
        <TabPanel>
          <div className="sites">
            {Object.keys(data).map((sites, i) =>
              data[sites].category === "component" ? (
                <div key={i}>
                  <a href={data[sites].url}>
                    <img src={data[sites].image} />
                  </a>
                  <a href={data[sites].url}>
                    <p>{data[sites].name}</p>
                  </a>
                </div>
              ) : null
            )}
          </div>
        </TabPanel>
        <TabPanel>
          <div className="sites">
            {Object.keys(data).map((sites, i) =>
              data[sites].category === "site" ? (
                <div key={i}>
                  <a href={data[sites].url}>
                    <img src={data[sites].image} />
                  </a>
                  <a href={data[sites].url}>
                    <p>{data[sites].name}</p>
                  </a>
                </div>
              ) : null
            )}
          </div>
        </TabPanel>
      </Tabs>
    </section>
  );
};

export default Portfolio;
